	import { mapGetters } from 'vuex';
	
export default {
	data() {
		return {
			count: 0,
			move: false
		};
	},
	props: {
		items:{
			type: Object,
			default: () => {}
		}
	},
	watch:{
		count(value) {
			if(value > 0) {
				this.move = true
			} else {
				this.move = false
			}
		},
		watchOption() {
			this.undate();
		}
		
	},
	created() {
		this.undate()
	},
computed: {
		...mapGetters(['watchOption'])
	},
	methods: {
		undate() {
			let that = this
			uni.getStorage({
				key: "goodsList",
				success: (res => {
					
					let bool = res.data.some((item) => {
					  return item.food_id == that.items.food_id;
					});  //先判断有没有存储都有和当前food_id相同都产品，没有count 为0，  有就循环把有相同food_id产品的food_count 赋值给count；如果不进行这一步，当food_count减小为0的时候，count的值依旧为1；
					if(bool) {
						res.data.forEach(function(item){
							if(item.food_id == that.items.food_id) {
								that.count = item.food_count
							} 
						})
					} else {
						that.count = 0
					}

				}),
				fail() {
					that.count = 0
				}
			})
			
		},
		addCart() {
			this.count++;
			this.$emit('add', this.count);
		},
		decreaseCart() {
			if (this.count) {
				this.count--;
			}
			this.$emit('sub', this.count);
		}
	}
};
